<template>
	<div id="app">
		<!--数据的显示 表达式和 v-text指令-->
		{{message}}
		<span v-text="info"></span>
		<!--双向数据绑定  v-model-->
		<input type="text" name="" id="" v-model="message">
			<hr />
			<ul>
				<li v-for="(v,index) in lists">{{index}}=={{v.id}}=={{v.title}}</li>
			</ul>
			<!--触发事件-->
			<button v-on:click="go()">点击</button>
			<!--简写-->
			<button @click="run()">重击</button>



		</div>
</template>

<script>


		export default {
				data(){
					return {
						message:"hello world",
					info:"我是vue",
					lists:[
						{
					    	id:1,
					    	title:'去吃饭'
					    },
					    {
					    	id:2,
					    	title:'打篮球'
					    },
					    {
					    	id:3,
					    	title:'看电视'
					    	},
					    {
					    	id:4,
					    	title:'听音乐'
					    	},
					    {
					    	id:5,
					    	title:'去睡觉'
					    	}

					]
					}
				},
				//$scope.message=""
				methods:{
					go(){
						console.log("go go go");
					},
					run(){
						console.log("run")
					}
				}
		}


</script>

<style>
</style>



