<template>
	<div id="app">
			<div class="panel panel-default">
			    <div class="panel-heading">
			   	    <h3 class="panel-title">Tolist({{total}})</h3>
			    </div>
			    <div class="panel-body">
			        <ul class="list-group">
			      	    <li :class="{done:item.completed}"
			      	    	v-for="(item,index) in todos"
			      	    	class="list-group-item">{{item.title}}{{item.completed}}
			      	        <button :class="item.completed?'btn-danger':'btn-success'
			      	        	" class="btn pull-right btn-xs"
			      	        	@click="toggle(index)">{{item.completed?'已完成':'完成'}}</button>
			      	        <button @click="deleteItem(index)"
			      	        	class="btn btn-info btn-xs pull-right">删除</button>
			      	    </li>

			        </ul>
			        <input type="text" class="form-control" v-model="newData.title" name="" id="" value="" />
			        <button @click="addNews(newData)" class="btn btn-success">添加</button>
			    </div>
			    <div class="panel-footer">

			    </div>
			</div>
		</div>

</template>

<script>
	export default {
				data(){
					return {
						newData:{id:null,title:"",completed:false},
					    todos:[
					    {
					    	id:1,
					    	title:'去吃饭',
					    	completed:false
					    },
					    {
					    	id:2,
					    	title:'去吃饭',
					    	completed:false
					    },
					    {
					    	id:3,
					    	title:'去吃饭',
					    	completed:false
					    },
					    {
					    	id:4,
					    	title:'去吃饭',
					    	completed:false
					    },
					    {
					    	id:5,
					    	title:'去吃饭',
					    	completed:false
					    }
					]

					}
				},
				methods:{
					addNews(data){
						this.todos.push(data);
						this.newData={
							id:null,
							title:'',
							completed:false
						}
					},
					toggle(index){
						this.todos[index].completed =
						!this.todos[index].completed;
					},
					deleteItem(index){
						this.todos.splice(index,1)
					}
				},
				computed:{
					total(){
						return this.todos.length;
					}
				}

	}
</script>

<style>
.done{
	    		color: green;
	    		text-decoration: line-through;
	    	}
				.list-group li{
					text-align: left;
				}
</style>



