import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import flag from '@/components/flag'
import todo from '@/components/todo'
import foraaa from '@/components/foraaa'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Hello
    },
    {
      path: '/flag',
      component: flag
    },
    {
      path: '/todo',
      component: todo
    },
    {
      path: '/foraaa',
      component: foraaa
    }
  ]
})



